@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}

.app {
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
  background:#f4f4f4;
  cursor: pointer;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
  color: black;
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: row;
  text-align: left;
  width: 65%;
  height: 100%;
  position: absolute;
  top: 0;
  left:0;
  background:#f4f4f4;
}

.auth-inner {
  margin: auto;
  background:#f4f4f4;
  width: 60%;
  padding: 100px;
  transition: all .3s;
}

.header {
  background: rgb(0, 0, 255);
  margin: auto;
  position: absolute;
  top:0;
  right:0;
  height: 100%;
  width: 35%;
}

.header-text {
  width: 250px;
  height: auto;
  margin: 200px 100px 100px 100px;
  padding: 20px;
  background: blue;
  align-content: center;
  
}

.h3-white {
  font-family: 'Fira Sans', sans-serif;
  color: white;
}

p {
  color: white;
  font-family: 'Fira Sans', sans-serif;
}
.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: left;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}
